html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 1.6rem;
  margin: 0 auto;
  color: rgb(255, 255, 255);
}

.container {
  display: flex;
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}

.section {
  width: 70%;
  margin: 5rem auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  color: rgb(255, 255, 255);
  margin: 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 2.8rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 1.8rem;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.main {
  width: 100vw;
  height: 100vh;
  background-image: url("/build/img/PabloBeglez0058.webp");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

.overlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.2);
  z-index: -1;
}

.header {
  padding: 2rem;
}
.elements-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo {
  width: 90%;
}
@media (min-width: 768px) {
  .logo {
    width: 60%;
  }
}
@media (min-width: 1024px) {
  .logo {
    width: 35rem;
  }
}

.instagram {
  margin-top: 2rem;
  width: 5rem;
}
.instagram a {
  width: 100%;
}
@media (min-width: 768px) {
  .instagram {
    width: 7rem;
  }
}

.footer {
  position: fixed;
  width: 100vw;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
}

.elements-footer {
  display: flex;
  padding: 0.5rem;
  flex-direction: column;
  align-items: center;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .elements-footer p {
    font-size: 2.6rem;
  }
}
@media (min-width: 1024px) {
  .elements-footer {
    width: 95%;
    flex-direction: row;
    justify-content: space-between;
    margin: 3rem auto;
  }
  .elements-footer p {
    font-size: 2rem;
    margin: 0 0.5rem;
  }
}

.info {
  text-align: center;
  margin-bottom: 1rem;
}
@media (min-width: 1024px) {
  .info {
    width: 70rem;
    display: flex;
    margin: 0;
  }
}

.policies {
  text-align: center;
}

.elements-sectionText {
  flex-direction: column;
}
@media (min-width: 768px) {
  .elements-sectionText {
    margin-top: 10rem;
  }
  .elements-sectionText h3 {
    font-size: 4.2rem;
  }
}